#footer {
  clear: both;
  position: relative;
  z-index: 2;
}
@media print, screen and (min-width: 768px) {
  #footer::before {
    content: "";
    display: block;
    width: 120%;
    height: 20em;
    position: absolute;
    top: 9em;
    left: -10%;
    background: #f2efea;
    border-radius: 50%;
    z-index: -1;
  }
}
@media print, screen and (min-width: 768px) {
  #footer::after {
    content: "";
    display: block;
    width: 100%;
    height: 15em;
    position: absolute;
    top: 19em;
    left: 0;
    background: #f2efea;
    z-index: -1;
  }
}

#footer .footerReservationBox {
  width: 90%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  margin-bottom: 2.5em;
}
@media print, screen and (min-width: 768px) {
  #footer .footerReservationBox {
    width: 63.5em;
    margin-bottom: 5em;
  }
}
#footer .footerReservationBox__bg img {
  width: 100%;
}
#footer .footerReservationBox__inner {
  display: block;
  margin-top: -9.5em;
}
@media print, screen and (min-width: 768px) {
  #footer .footerReservationBox__inner {
    margin-top: 0;
    width: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}
#footer .footerReservationBox__title {
  display: block;
  text-align: center;
  margin-top: 2em;
  margin-bottom: 0.7em;
  font-size: 1.4em;
